/* Reset de Estilos */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  /* text-decoration: none; */
}

/* Variables */

:root {
  --primarygreen: rgb(67, 169, 57);
  --contrastgreen: rgb(56, 150, 47);
  --primaryblack: rgba(0, 0, 0, 0.96);
}

.experiencia-container {
  padding: 20px 0 60px 0;
  width: 100%;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experiencia-titulo {
  font-size: 50px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;

  width: 60%;
  margin: 0px auto;
  padding-bottom: 10px;

  text-align: center;

  border-bottom: 1px solid rgb(24, 24, 24);
  color: rgb(24, 24, 24);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 10px;
  left: 49.97%;
  margin-left: -0.0625rem;
  width: 0.225rem;
  height: 100%;
  background-color: rgb(197, 197, 197);
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.14rem solid rgb(66, 66, 66);
  background-color: rgb(41, 40, 40);
  margin-top: 32px;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  border: 2px solid rgb(41, 41, 41);
  border-radius: 5px;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    144deg,
    rgba(0, 0, 0, 0.82) 0%,
    rgba(10, 10, 10, 0.8625333727241) 45%,
    rgba(25, 25, 25, 1) 96%
  );
  box-shadow: 1px 1px 7px var(--primaryblack);
  padding: 5px 20px;
  color: rgb(247, 240, 240);
  direction: ltr;
  font-family: "Montserrat", sans-serif;
  margin: 10px 0;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: rgba(255, 255, 255, 0.87);

  font-weight: 900;
  font-size: 25px;
}

.timeline-content .empresa {
  margin-top: 0.3125rem;
  color: rgba(252, 249, 249, 0.87);
  width: 100%;
  margin: 0;
  font-size: 18px;
}

.timeline-content .cargo {
  line-height: 35px;
  font-size: 17px;
  color: rgb(231, 225, 225);
  border-bottom: 1px solid rgba(233, 231, 231, 0.534);
  padding: 5px 10px;
  text-align: right;
}

.timeline-content .descripcion-puesto {
  line-height: 35px;
  font-size: 16px;
  color: rgb(241, 241, 241);
  font-style: italic;
  text-align: justify;
}

/********** QUERIES *********/

