}

.line {
  height: 25px;
  width: 100%;
  background-color: rgb(155, 154, 154);
  position: relative;
}

.line::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 80%;
  left: 0;
  top: 0;
  border-right: 1px solid black;
  background-color: var(--primarygreen);
}

.html::before {
  width: 70%;
}

.css::before {
  width: 50%;
}

.js::before {
  width: 40%;
}

.sql::before {
  width: 40%;
}

.node::before {
  width: 40%;
}

.react::before {
  width: 50%;
}

.figma::before {
  width: 50%;
}
.atencioncliente::before {
  width: 90%;
}

.español::before {
  width: 100%;
}

.gallego::before {
  width: 100%;
}

.ingles::before {
  width: 70%;
}

.aleman::before {
  width: 30%;
}

.creatividad::before {
  width: 80%;
}

.adaptabilidad::before {
  width: 80%;
}

